
@charset "UTF-8";
.input-wrapper {
  width: 100%;
  height: 2.25rem;
  text-align: left;
  font-size: 0.65rem;
  line-height: 2.25rem;
  background-color: #fff;
  padding: 0 3%;
  display: block;
}
.input-wrapper .input-wrapper-box {
    border-bottom: 0.025rem solid #eee;
    height: 100%;
    width: 100%;
}
.input-wrapper .input-wrapper-box div {
      float: left;
}
.input-wrapper .input-wrapper-box .title-box {
      float: left;
      display: inline-block;
      width: 28%;
}
.input-wrapper .input-wrapper-box .input-box {
      width: 72%;
      height: 80%;
}
.input-wrapper .input-wrapper-box .input-box input {
        height: 100%;
        width: 100%;
}
.input-wrapper .input-wrapper-box .date-choose {
      display: inline-block;
      height: 100%;
      width: 72%;
}
.input-wrapper .input-wrapper-box .date-choose input {
        display: inline-block;
        width: 30%;
}
.input-wrapper .input-wrapper-box .date-choose i {
        float: right;
        position: relative;
        top: 50%;
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
}

/*input-wrapper*/
.day-rent-Detail {
  width: 100%;
  height: 100%;
  margin: 0;
  /*middle-wrapper end */
  /*footer-wrapper end */
}
.day-rent-Detail .middle-wrapper {
    /*middle-top-content end */
    /*detail-box-wrapper end*/
}
.day-rent-Detail .middle-wrapper .go-icon {
      float: right;
      position: relative;
      top: 50%;
      /*偏移*/
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
}
.day-rent-Detail .middle-wrapper h1 {
      width: 100%;
      color: #676767;
      text-align: left;
      font-size: 0.65rem;
}
.day-rent-Detail .middle-wrapper .middle-top-content {
      background-color: #fff;
}
.day-rent-Detail .middle-wrapper .middle-top-content .carousel-wrapper {
        height: 10.95rem;
        width: 100%;
        background-color: #fff;
}
.day-rent-Detail .middle-wrapper .middle-top-content .middle-top-detail {
        text-align: left;
        font-size: 0.75rem;
        padding: 0.875rem 3% 0.625rem 3%;
}
.day-rent-Detail .middle-wrapper .middle-top-content .middle-top-detail h1 {
          margin-bottom: 0.7rem;
          color: #333;
}
.day-rent-Detail .middle-wrapper .middle-top-content .middle-top-detail p {
          color: #cc0000;
}
.day-rent-Detail .middle-wrapper .middle-top-content .middle-top-detail p em:last-child {
            float: right;
            margin-right: 10.66%;
}
.day-rent-Detail .middle-wrapper .middle-top-content .house-rent-detail {
        text-align: left;
        font-size: 0.75rem;
        padding: 0.875rem 3% 0.625rem 3%;
}
.day-rent-Detail .middle-wrapper .middle-top-content .house-rent-detail h1 {
          margin-bottom: 0.7rem;
          color: #333;
}
.day-rent-Detail .middle-wrapper .middle-top-content .house-rent-detail span {
          font-size: 0.6rem;
          color: #676767;
}
.day-rent-Detail .middle-wrapper .middle-top-content .house-rent-detail em {
          float: right;
          color: #cc0000;
          margin-right: 10.66%;
}
.day-rent-Detail .middle-wrapper .middle-top-content .house-sell-detail {
        font-size: 0.65rem;
        padding: 1rem 0;
        border-top: 0.025rem solid #f4f4f4;
}
.day-rent-Detail .middle-wrapper .middle-top-content .house-sell-detail div {
          display: inline-block;
          width: 30%;
          border-right: 0.025rem solid #eee;
}
.day-rent-Detail .middle-wrapper .middle-top-content .house-sell-detail div:last-child {
            border: none;
}
.day-rent-Detail .middle-wrapper .middle-top-content .house-sell-detail div span {
            display: block;
}
.day-rent-Detail .middle-wrapper .middle-top-content .house-sell-detail div em {
            color: #cc0000;
}
.day-rent-Detail .middle-wrapper .little-box {
      background-color: #fff;
      height: 2.5rem;
      line-height: 2.5rem;
      margin-top: 0.5rem;
      font-size: 0.6rem;
      text-align: left;
      padding: 0 3%;
}
.day-rent-Detail .middle-wrapper .rent-date p {
      width: 100%;
      height: 100%;
}
.day-rent-Detail .middle-wrapper .rent-date p i {
        display: inline-block;
        vertical-align: middle;
}
.day-rent-Detail .middle-wrapper .rent-date p i.date-icon {
          height: 0.925rem;
          width: 0.925rem;
          background: url("/static/img/rizu_day.png") no-repeat;
          background-size: 100% 100%;
          margin-right: 3%;
}
.day-rent-Detail .middle-wrapper .evaluation-box {
      color: #676767;
      /*room-evaluation-list end */
}
.day-rent-Detail .middle-wrapper .evaluation-box p {
        width: 100%;
        height: 2.5rem;
        border-bottom: 0.02rem solid #f4f4f4;
}
.day-rent-Detail .middle-wrapper .evaluation-box p .toEvaluationBox {
          display: inline-block;
          height: 100%;
          width: 90%;
}
.day-rent-Detail .middle-wrapper .evaluation-box p .toEvaluationBox span:nth-child(2) {
            color: #cc0000;
}
.day-rent-Detail .middle-wrapper .evaluation-box p .toEvaluationBox span:nth-child(3) {
            margin-left: 5%;
}
.day-rent-Detail .middle-wrapper .evaluation-box p .go-box {
          display: inline-block;
          width: 10%;
          height: 100%;
          float: right;
}
.day-rent-Detail .middle-wrapper .evaluation-box .room-evaluation-list {
        padding: 0 3%;
        font-size: 0.6rem;
        background-color: #fff;
}
.day-rent-Detail .middle-wrapper .evaluation-box .room-evaluation-list .room-evaluation-box {
          text-align: left;
          margin-bottom: 2.15rem;
          width: 100%;
}
.day-rent-Detail .middle-wrapper .evaluation-box .room-evaluation-list .room-evaluation-box h1 {
            margin-top: 1.25rem;
            margin-bottom: 0.65rem;
}
.day-rent-Detail .middle-wrapper .evaluation-box .room-evaluation-list .room-evaluation-box h1 em {
              float: right;
              color: #666;
}
.day-rent-Detail .middle-wrapper .evaluation-box .room-evaluation-list .room-evaluation-box p {
            font-size: 0.5rem;
            color: #656565;
            line-height: 0.9rem;
}
.day-rent-Detail .middle-wrapper .detail-box-wrapper {
      background-color: #fff;
      margin-top: 0.5rem;
      padding: 0 3%;
      font-size: 0.65rem;
}
.day-rent-Detail .middle-wrapper .detail-box-wrapper .detail-top {
        padding-bottom: 0.875rem;
        border-bottom: 0.025rem solid #f3f3f3;
}
.day-rent-Detail .middle-wrapper .detail-box-wrapper .detail-top .detail-top-box {
          width: 100%;
}
.day-rent-Detail .middle-wrapper .detail-box-wrapper .detail-top .detail-top-box div {
            display: inline-block;
            float: left;
            width: 50%;
            text-align: left;
            margin-top: 0.8rem;
}
.day-rent-Detail .middle-wrapper .detail-box-wrapper .detail-top .detail-top-box div span {
              color: #676767;
}
.day-rent-Detail .middle-wrapper .detail-box-wrapper .detail-bottom {
        margin-top: 0.55rem;
        padding-bottom: 1.5rem;
}
.day-rent-Detail .middle-wrapper .detail-box-wrapper .detail-bottom p {
          text-align: left;
          font-size: 0.6rem;
}
.day-rent-Detail .middle-wrapper .detail-box-wrapper .detail-bottom p span {
            display: inline-block;
            width: 24%;
            margin-top: 0.875rem;
            vertical-align: middle;
}
.day-rent-Detail .middle-wrapper .detail-box-wrapper .detail-bottom p i {
            display: inline-block;
            width: 0.625rem;
            height: 0.625rem;
            background: url("/static/img/action.png") no-repeat;
            background-size: 100% 100%;
            vertical-align: middle;
            margin-right: 0.5rem;
}
.day-rent-Detail .middle-wrapper .about-around {
      width: 100%;
      margin-top: 0.5rem;
      background-color: #fff;
      padding: 0.8rem 3% 0 3%;
}
.day-rent-Detail .middle-wrapper .about-around article .article-container {
        margin: 0.5rem 0;
        text-align: left;
        font-size: 0.65rem;
        line-height: 0.9rem;
        overflow: hidden;
}
.day-rent-Detail .middle-wrapper .about-around article .article-container img {
          width: 100%;
}
.day-rent-Detail .middle-wrapper .about-around .img-box {
        width: 100%;
}
.day-rent-Detail .middle-wrapper .about-around .img-box img {
          width: 100%;
          margin-top: 0.5rem;
}
.day-rent-Detail .middle-wrapper .about-around .show-icon {
        width: 100%;
        height: 1.5rem;
        margin: 0;
        padding-top: 0.5rem;
}
.day-rent-Detail .middle-wrapper .about-around .show-icon i {
          display: block;
          height: 0.35rem;
          width: 0.7rem;
          background: url("/static/img/ico_02.png") no-repeat;
          background-size: 100% 100%;
          margin: 0 auto;
}
.day-rent-Detail .footer-wrapper {
    position: fixed;
    bottom: 0;
    left: 0;
    height: 2.5rem;
    width: 100%;
    line-height: 2.5rem;
    border-top: 0.025rem solid #c0c0c3;
    font-size: 0.5rem;
    color: #a7a7a8;
}
.day-rent-Detail .footer-wrapper div, .day-rent-Detail .footer-wrapper button {
      width: 50%;
      display: inline-block;
      float: left;
      text-align: center;
}
.day-rent-Detail .footer-wrapper button[disabled] {
      background-color: #ccc;
}
.day-rent-Detail .footer-wrapper div {
      background-color: #fff;
      font-size: 0.6rem;
      color: #686868;
      padding-top: 0.4rem;
}
.day-rent-Detail .footer-wrapper div i {
        display: block;
        width: 0.925rem;
        height: 0.825rem;
        margin: 0 auto;
}
.day-rent-Detail .footer-wrapper div i.save {
          background: url("/static/img/chuzhu_shoucang.png") no-repeat;
          background-size: 100% 100%;
}
.day-rent-Detail .footer-wrapper div i.unSave {
          background: url("/static/img/chuzhu_shoucang01.png") no-repeat;
          background-size: 100% 100%;
}
.day-rent-Detail .footer-wrapper div p {
        display: block;
        height: 1.25rem;
        line-height: 1.25rem;
        text-align: center;
}
.day-rent-Detail .footer-wrapper button {
      background-color: #51ca94;
      font-size: 0.75rem;
      color: #fff;
      height: 100%;
      bdaily: 0;
}



